<template>
  <div>
    <div style="margin:10px">
      <h2></h2>
    </div>
    <Card class='card'>
      <h3 slot="title"></h3>
      <Button type="primary" @click="Minfo"></Button>
      <Button type="success" @click="Msuccess"></Button>
      <Button type="warning" @click="Mwarning"></Button>
      <Button type="error" @click="Merror"></Button>
      <Button @click="Mloading"></Button>
      <Button @click="Mtime">10</Button>
      <Button @click="Mclosable"></Button>
    </Card>
    <Card class='card'>
      <h3 slot="title"></h3>
      <p style="margin:10px">+</p>
      <Button type="primary" @click="Ninfo(false)"></Button>
      <Button type="success" @click="Nsuccess(false)"></Button>
      <Button type="warning" @click="Nwarning(false)"></Button>
      <Button type="error" @click="Nerror(false)"></Button>
      <p style="margin:10px"></p>
      <Button type="primary" @click="Ninfo()"></Button>
      <Button type="success" @click="Nsuccess(true)"></Button>
      <Button type="warning" @click="Nwarning(true)"></Button>
      <Button type="error" @click="Nerror(true)"></Button>
      <p style="margin:10px"></p>
      <Button @click="Ninfo2()"></Button>
    </Card>
    <Card class='card'>
      <h3 slot="title"></h3>
      <p style="margin:10px"></p>
      <Button type="primary" @click="Moinstance('info')"></Button>
      <Button type="success" @click="Moinstance('success')"></Button>
      <Button type="warning" @click="Moinstance('warning')"></Button>
      <Button type="error" @click="Moinstance('error')"></Button>
    </Card>
  </div>
</template>
<script type="text/javascript">
  export default {
    name: 'DMessage',
    data() {
      return {}
    },
    methods: {
      Minfo() {
        this.$Message.info('');
      },
      Msuccess() {
        this.$Message.success('');
      },
      Mwarning() {
        this.$Message.warning('');
      },
      Merror() {
        this.$Message.error('');
      },
      Mloading() {
        const msg = this.$Message.loading({
          content: 'Loading...',
          duration: 0
        });
        setTimeout(msg, 3000);
      },
      Mclosable() {
        this.$Message.info({
          content: '',
          duration: 10,
          closable: true
        });
      },
      Mtime() {
        this.$Message.info({
          content: '',
          duration: 10
        });
      },
      Ninfo(nodesc) {
        this.$Notice.info({
          title: '',
          desc: nodesc ? '' : ''
        });
      },
      Ninfo2(nodesc) {
        this.$Notice.info({
          title: '',
          desc: '',
          duration: 0
        });
      },
      Nsuccess(nodesc) {
        this.$Notice.success({
          title: '',
          desc: nodesc ? '' : ''
        });
      },
      Nwarning(nodesc) {
        this.$Notice.warning({
          title: '',
          desc: nodesc ? '' : ''
        });
      },
      Nerror(nodesc) {
        this.$Notice.error({
          title: '',
          desc: nodesc ? '' : ''
        });
      },
      Moinstance (type) {
        const title = '';
        const content = '<p></p>';
        switch (type) {
          case 'info':
          this.$Modal.info({
            title: title,
            content: content
          });
          break;
          case 'success':
          this.$Modal.success({
            title: title,
            content: content
          });
          break;
          case 'warning':
          this.$Modal.warning({
            title: title,
            content: content
          });
          break;
          case 'error':
          this.$Modal.error({
            title: title,
            content: content
          });
          break;
        }
      }
    }
  }

</script>
<style scoped>
.card {
  margin: 10px 0 10px 0;
}

</style>



