<template>
  <Row>
    <Col span='12'>
    <Card class='color'>
      <h3 slot="title"></h3>
      <Tag checkable color="blue"></Tag>
      <Tag checkable color="green"></Tag>
      <Tag checkable color="red"></Tag>
      <Tag checkable color="yellow"></Tag>
    </Card>
    <Card class='color'>
      <h3 slot="title"></h3>
      <Avatar icon="person" />
      <Avatar>U</Avatar>
      <Avatar>USER</Avatar>
      <Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" />
      <Avatar style="color: #f56a00;background-color: #fde3cf">U</Avatar>
      <Avatar style="background-color: #87d068" icon="person" />
      <Badge count="1">
        <Avatar shape="square" icon="person" />
      </Badge>
    </Card>
    <Card class='color'>
      <h3 slot="title"></h3>
      <Timeline>
        <TimelineItem>
          <p class="time">1976</p>
          <p class="content">Apple I </p>
        </TimelineItem>
        <TimelineItem>
          <p class="time">1984</p>
          <p class="content"> Macintosh</p>
        </TimelineItem>
        <TimelineItem>
          <p class="time">2007</p>
          <p class="content"> iPhone</p>
        </TimelineItem>
        <TimelineItem>
          <p class="time">2010</p>
          <p class="content"> iPad</p>
        </TimelineItem>
        <TimelineItem>
          <p class="time">2011105</p>
          <p class="content"></p>
        </TimelineItem>
      </Timeline>
    </Card>
    <Card class='color'>
      <h3 slot="title"></h3>
      <Steps :current="2" direction="vertical">
        <Step title="" content=""></Step>
        <Step title="" content=""></Step>
        <Step title="" content=""></Step>
        <Step title="" content=""></Step>
      </Steps>
    </Card>
    <Card class='color'>
      <h3 slot="title"></h3>
      <Slider v-model="valueSlider1" />
      <Slider v-model="valueSlider2" range/>
      <Slider v-model="valueSlider1" :step="5" show-stops />
    </Card>
  </Col>
  <Col span='12'>
  <Card class='color'>
    <h3 slot="title"></h3>
    <Rate allow-half show-text v-model="valueHalf" />
  </Card>
  <Card class='color' :style='{background:color}'>
    <h3 slot="title"></h3>
    <ColorPicker v-model="color" recommend alpha />
  </Card>
  <Card class='color'>
    <h3 slot="title"></h3>
    <Upload multiple type="drag" action="//jsonplaceholder.typicode.com/posts/">
      <div style="padding: 20px 0">
        <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
        <p></p>
      </div>
    </Upload>
  </Card>
  <Card class='color'>
    <h3 slot="title"></h3>
    <Carousel v-model="valueLoop" loop>
      <CarouselItem>
        <div class="demo-carousel">1</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">2</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">3</div>
      </CarouselItem>
      <CarouselItem>
        <div class="demo-carousel">4</div>
      </CarouselItem>
    </Carousel>
  </Card>
  <Card class='color'>
    <h3 slot="title"></h3>
    <Progress :percent="25"></Progress>
    <Progress :percent="45" status="active"></Progress>
    <Progress :percent="65" status="wrong"></Progress>
    <Progress :percent="100"></Progress>
    <Progress :percent="25" hide-info></Progress>
  </Card>
  <Card class='color'>
    <h3 slot="title"></h3>
    <Page :total="100" show-total></Page>
  </Card>
  <Card class='color'>
    <h3 slot="title"></h3>
    <Tooltip content="Top Left text" placement="top-start">
      <Button></Button>
    </Tooltip>
    <Tooltip placement="top">
        <Button></Button>
        <div slot="content">
            <p></p>
            <p><i></i></p>
        </div>
    </Tooltip>
    <Tooltip content="Top Right text" placement="top-end">
      <Button></Button>
    </Tooltip>
  </Card>
</Col>

<BackTop></BackTop>
</Row>
</template>
<script type="text/javascript">
  export default {
    name: 'DStuff',
    data() {
      return {
        valueText: 3,
        valueHalf: 2.5,
        color: '#fff',
        valueLoop: 1,
        valueSlider1: 10,
        valueSlider2: [10, 20]
      }
    }
  }

</script>
<style type="text/css" scoped>
.color {
  padding: 8px;
  margin: 18px;
}

.demo-carousel {
  background: #506b9e;
  height: 100px;
  line-height: 100px;
  text-align: center;
  color: #fff;
}

</style>



